<template>
  <v-container class="fill-height" justify-center fluid>
    <v-row align="center" justify="center"
      v-show="!$store.state.credentials.authenticated"
    >
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12">
          <v-toolbar color="primary" dark flat>
            <v-toolbar-title>Login</v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-form>
              <v-text-field label="Email" name="email"
                prepend-icon="mdi-email" type="email" :readonly="loading"
                v-model="email" :error="loginError" clearable
              />
              <v-text-field label="Password" name="password"
                prepend-icon="mdi-lock" type="password" :readonly="loading"
                v-model="password" :error="loginError" clearable
              />
              <v-checkbox v-model="remember" label="Remember me"></v-checkbox>
            </v-form>
          </v-card-text>
          <v-card-actions>
            <v-btn text color="primary" small
              :disabled="loading" to="/forgot"
            >Forgot</v-btn>
            <v-btn text color="primary" small
              :disabled="loading" to="/register"
            >Register</v-btn>
            <v-spacer />
            <v-btn color="primary" @click="btnLoginClick"
              :loading="loading" :disabled="!inputOK"
            >Login</v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-row align="center" justify="center"
      v-show="$store.state.credentials.authenticated"
    >
      <v-col cols="12" class="text-center">
        Loading...
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
"use strict"

import { login } from "@/lib/auth"

export default {
  name: "Login",
  data: () => ({
    email: "",
    password: "",
    remember: true,
    loginError: false,
    loading: false
  }),
  computed: {
    inputOK() {
      try {
        if (0 === this.email.length) return false
        if (0 === this.password.length) return false
        return true
      } catch {
        return false
      }
    }
  },
  methods: {
    btnLoginClick: async function() {
      this.loginError = false
      this.loading = true
      let resp = await login(this.email, this.password, this.remember)
      this.loading = false
      if (200 === resp.code) {
        if (Object.prototype.hasOwnProperty.call(this.$route.query, "redirect")) {
          this.$router.replace(this.$route.query.redirect)
        } else {
          this.$router.replace("/")
        }
      } else if (401 === resp.code){
        this.loginError = true
      } else {
        this.loginError = true
        alert(resp)
      }
    }
  }
}
</script>
